export default {
	data() {
		return {
			animate: false,
			marqueeList: [],
			// defaultURL:'http://cdn.douke.51dreaming.com',
			defaultURL: this.util.uploaddata.cdnurl,
			background: ['color1', 'color2', 'color3'],
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500,
			data: null,
		}
	},
	created: function() {
		setInterval(this.showMarquee, 2000)
	},
	onLoad() {
		let params = {};
		let url = "/api/index/index";
		this.util.request(url, "GET", params, (res) => {
			if (res.statusCode == 200) {
				if (res.data.code == 1) {
					this.data = res.data.data;
					this.marqueeList = this.data.notice;
					console.log(JSON.stringify(this.data));
				} else {
					this.util.showWindow("后台请求失败");
				}
			} else {
				this.util.showWindow("请求失败");
			}
		});
	},
	mounted() {

		// uni.navigateTo({
		// 	url:"../../me/vip-center/vip-center"
		// })
	},
	methods: {
		navito(url) {
			window.location.href = url;
		},
		changeIndicatorDots(e) {
			this.indicatorDots = !this.indicatorDots
		},
		changeAutoplay(e) {
			this.autoplay = !this.autoplay
		},
		intervalChange(e) {
			this.interval = e.target.value
		},
		durationChange(e) {
			this.duration = e.target.value
		},
		help() {
			uni.navigateTo({
				url: '../help/index',
				animationType: 'slide-in-right',
				animationDuration: 300
			});
		},
		becomevip() {
			uni.navigateTo({
				url: '../becomevip/index/index',
				animationType: 'slide-in-right',
				animationDuration: 300
			});
		},
		task_detail(id) {
			uni.navigateTo({
				url: "../../task/task-detail/task-detail?id=" + id
			})
		},
		howsave: function() {
			uni.navigateTo({
				url: "../../me/how-save/how-save"
			})
		},
		showMarquee: function() {
			this.animate = true;
			setTimeout(() => {
				this.marqueeList.push(this.marqueeList[0]);
				this.marqueeList.shift();
				this.animate = false;
			}, 500)
		},

	}
}
